export default {
	data() {
		return {
			selectCity: '杭州市',
			search: '',
		};
	},
	onLoad() {
		//判断城市数据,如果没有,就重新请求一次.
		if (this.selectCity) {} else {
			this.getLocation();
		}
	},
	onShow() {
		const selectCity = uni.getStorageSync('selectCity');
		if(selectCity){
			this.selectCity = selectCity;
		}
	},
	methods: {
		// 跳转到城市选择页面
		selectCityPage(){
			uni.navigateTo({
				url: '../city/city',
			});
		},
		// 获取位置信息
		getLocation() {
			uni.getLocation({
				type: 'wgs84',
				success: res => {
					console.log(res);
					let latitude = res.latitude;
					let longitude = res.longitude;
					uni.request({
						url: 'https://apis.map.qq.com/ws/geocoder/v1/?location=' + latitude + ',' + longitude +
							'&key=UGMBZ-S5AKU-YQGV3-47M5J-BAQ62-ZBBJW',
						success: data => {
							console.log(data);
							this.selectCity = data.data.result.address_component.city;
						}
					});
				}
			});
		},
	}
}
